import { Component, OnInit } from '@angular/core';
import { Drizzle } from '@drizzle/store';
import { SmartContractService } from 'src/app/services/smart-contract.service';
import { UtilsService } from 'src/app/services/utils.service';

@Component({
  selector: 'app-mobile',
  templateUrl: './mobile.component.html',
  styleUrls: ['./mobile.component.scss']
})
export class MobileComponent implements OnInit {

  hidesubmenu: boolean = true;
  showsidebar: boolean = true;
  drizzle?: Drizzle = null;

  constructor(
    private smartContract: SmartContractService,
    private utils: UtilsService
  ) { }

  ngOnInit(): void {
    this.smartContract.drizzleObservable.subscribe((drizzle) => {
      this.drizzle = drizzle;
      console.log(drizzle);
      alert(drizzle);
    });
  }

  connect(){
    this.smartContract.connectWallet();
  }

  hideSidebar(){
    this.showsidebar = true;
  }

  showSidebar(){
    this.showsidebar = false;
  }

  hideSubmenu(){
    this.hidesubmenu = false;
  }

  showSubmenu(){
    this.hidesubmenu = true;
  }

  changeConnectButtonText(address: string){
    return this.utils.showCurrentAccountAddressButton(address);
  }

}
