import { BrowserModule } from '@angular/platform-browser';
import { CUSTOM_ELEMENTS_SCHEMA, NgModule } from '@angular/core';

import { AppRoutingModule } from './app-routing.module';
import { AppComponent } from './app.component';
import { AppBootstrapModule } from './modules/app-bootstrap/app-bootstrap.module';
import { BrowserAnimationsModule } from '@angular/platform-browser/animations';
import { MaterialModule } from './modules/material/material.module';
import { HeaderComponent } from './components/header/header.component';
import { FooterComponent } from './components/footer/footer.component';

import {TranslateLoader, TranslateModule} from '@ngx-translate/core';
import {TranslateHttpLoader} from '@ngx-translate/http-loader';
import {HttpClient, HttpClientModule} from '@angular/common/http';
import { NgbModule } from '@ng-bootstrap/ng-bootstrap';
import { MobileComponent } from './components/header/mobile/mobile.component';
import { PcComponent } from './components/header/pc/pc.component';
import { HomeComponent } from './components/home/home.component';
import { RouterModule, Routes } from '@angular/router';
import { OffersComponent } from './components/offers/offers.component';
import { OffersTokenComponent } from './components/offers-token/offers-token.component';
import { MyOffersComponent } from './components/my-offers/my-offers.component';
import { PurchaseComponent } from './components/purchase/purchase.component';
import { MyPurchasesComponent } from './components/my-purchases/my-purchases.component';
import { BuyComponent } from './components/buy/buy.component';
import { SellComponent } from './components/sell/sell.component';
import { ReactiveFormsModule } from '@angular/forms';
import { TransferService } from './services/transfer.service';
import { SmartContractService } from './services/smart-contract.service';

const appRoutes: Routes = [
  {path: '', component: HomeComponent},
  {path: 'my-offers', component: MyOffersComponent},
  {path: 'offers-token/:symbol', component: OffersTokenComponent},
  {path: 'sell', component: SellComponent},
  /*{path: 'dashboard', canActivate: [AuthGuard], component: DashboardComponent},
  {path: 'customize/fb/bot/:id', canActivate: [AuthGuard], component: CustomBotFbComponent},
  {path: 'accounts/providers', canActivate: [AuthGuard], component: AccountComponent},
  /*{path: 'messages', canActivate: [AuthGuardService],component: MessageComponent},
  /*{path: 'messages/:idReceiver', canActivate: [AuthGuardService],component: MessageComponent},
  {path: 'message/read', canActivate: [AuthGuardService],component: ReadMessageComponent},
  {path: 'members', canActivate: [AuthGuardService],component: MemberComponent},
  {path: 'profile', canActivate: [AuthGuardService],component: ProfileComponent},
  {path: 'logout', canActivate: [AuthGuardService],component: LogoutComponent}*/
];

@NgModule({
  declarations: [
    AppComponent,
    HeaderComponent,
    FooterComponent,
    MobileComponent,
    PcComponent,
    HomeComponent,
    OffersComponent,
    OffersTokenComponent,
    MyOffersComponent,
    PurchaseComponent,
    MyPurchasesComponent,
    BuyComponent,
    SellComponent
  ],
  imports: [
    BrowserModule,
    AppRoutingModule,
    AppBootstrapModule,
    RouterModule.forRoot(appRoutes),
    BrowserAnimationsModule,
    ReactiveFormsModule,
    MaterialModule,
    HttpClientModule,
    TranslateModule.forRoot({
        loader: {
            provide: TranslateLoader,
            useFactory: HttpLoaderFactory,
            deps: [HttpClient]
        }
    }),
    NgbModule,
  ],
  providers: [
    //TransferService
    SmartContractService
  ],
  bootstrap: [AppComponent],
  schemas: [CUSTOM_ELEMENTS_SCHEMA]
})
export class AppModule { }

export function HttpLoaderFactory(http: HttpClient): TranslateHttpLoader {
  return new TranslateHttpLoader(http);
}
